.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  color: #00496B;
  font-family: Courier;
  max-height: 100%;
}

.player-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.5em;
  width: 100%;
}

.player-container table {
  border-collapse: collapse;
  text-align: center;
  width: 25%;
  display: inline-block;
  margin: 2em 2em 0.5em 2em;
  height: 100%;
  max-height:500px;
}

.player-container table td {
  margin: 2em 2em 0.5em 2em;
  padding: 1em 2em;
  border-left: 2em solid #fff;
  background: #E1EEF4;
  width: 50%;
}

tr:first-child td:first-child {
  border-top-left-radius: 10px;
}
tr:first-child td:last-child {
  border-top-right-radius: 10px;
}
tr:last-child td:first-child {
  border-bottom-left-radius: 10px;
}
tr:last-child td:last-child {
  border-bottom-right-radius: 10px;
}

table td:first-child {
  border-left: none;
 }

img {
  border-radius: 10px;
  border:3px solid #00496B;
  max-width: 250px;
  height:250px;
}

.gameplay {
  width: 50%;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.gameplay table {
  border-collapse: collapse;
  border-radius: 10px;
  background: #E1EEF4;
  display: inline-block;
  padding: 0.5em;
  margin: 0.5em;
}



